.wireframe-image {
  width: 300px;
  height: 200px;
  margin: auto;
  display: block;
  cursor: pointer;
}

.transparent-layer {
    background-color: blue;
}

.transparent-layer:hover {
    background-color: gray;
}

.enlargedImageContainer {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #333333;
  opacity: 0.97;
  z-index: 99999;
}

.enlargedImage {
  max-width: 70%;
  max-height: 70%;
  position: relative;
  margin: auto;
  display: block;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
}

.closeEnlargedImage {
  border: none;
  outline: none;
  position: absolute;
  right: 20px;
  top: 20px;
  background-color: #333333;
  color: white;
  cursor: pointer;
}

